.container {
    background-color: #171923;
    position: fixed;
    height: 100%;
    top: 0px;
    left: 0px;
    width: 200px;
    animation: showSidebar 0.5s;
    box-shadow: 0 0 20px 3px;

}

.sidebarList {
    height: auto;
    padding: 0%;
    width: 100%;
}

.sidebarList .row {
    height: 60px;
    width: 100px;
    background-color: #171923;
    list-style-type: none;
    margin: 0%;
    display: flex;
    flex-direction: row;
    color: rgb(255, 255, 255);
    justify-content: center;
    align-items: center;
    font-family: Arial, Helvetica, sans-serif;
}

.sidebarList .row :hover {
    color: #0e3664;
    cursor: pointer;
}

.row #icons {
    flex: 30%;
    display: grid;
    place-items: center;

}

.row #title {
    flex: 70%;
}

.container.closed {
    left: -100%;
}

.container svg {
    color: white;
    width: 24px;
    height: 24px;
    margin-left: 24px;
    cursor: pointer;
}

@keyframes showSidebar {
    from {
        opacity: 0;
        width: 0;
    }

    to {
        opacity: 2;
        width: 200px;
    }
}