.nav {
  height: 100%;
  display: flex;
  justify-content: space-between;
  padding: 1rem;
  background-color: #06283D;
  box-shadow: 0 0 20px 3px;
  align-items: center;
}

.nav svg {
  width: 24px;
  height: 24px;
  vertical-align: middle;
  fill: white;
  margin-right: 8px;
}



.topnav {
  overflow: hidden;
  display: flex;
  align-content: center;
  top: 0%;
  right: 40px;
  font-family: "Roboto", sans-serif;
  display: flex;
  align-items: center;
  border: 1px solid #ccc;
  border-radius: 5px;
  overflow: hidden;
}



.topnav button[type='submit'] {
  cursor: pointer;
  padding: 8px;
  border: salmon;
  outline: none;
  border-radius: 10px;
  background-color: #06283D;
  flex: 1;
}

.Search button:hover {
  background-color: #0cc6ff;
  color: #06283D;
  cursor: pointer;
  border-radius: 10px;
}

.navbar_Search {
  display: contents;
  align-items: center;
  justify-content: center;
  justify-items: center;
}

.Search {
  display: flex;
  height: 2rem;
  width: auto;
  margin: 0 0 0 40px;
  padding: 0 4px;
  flex: 1;
  flex-basis: 1e-9px;
}

.search-input {
  border-radius: 5%;
}

.search-button {
  height: inherit;
  cursor: pointer;
  border: salmon;
  outline-offset: 2cap;
  border-radius: 10px;
}


/* Style the search box inside the navigation bar */
.Search input[type=text] {
  float: left;
  padding: 8px;
  border: 1px solid blanchedalmond;
  border-top-left-radius: 5px;
  outline: none;
  width: 300px;
  font-size: 17px;
}

/* When the screen is less than 600px wide, stack the links and the search field vertically instead of horizontally */
@media screen and (max-width: 600px) {

  .topnav input[type=text] {
    float: none;
    display: block;
    text-align: left;
    width: 100%;
    margin: 0;
    padding: 14px;
  }

  .topnav input[type=text] {
    border: 1px solid #ff0707;
  }
}